	module.exports = {"data":{"markdownRemark":{"html":"<h2>Retrospectives</h2>\n<p>The Sprint Retrospective can really be the most useful and important of the scrum ceremonies.</p>\n<p>The concept of <em>“Inspect and Adapt”</em> is crucial to creating a successful and thriving team.</p>\n<p>The standard agenda for the retrospective is:</p>\n<ul>\n<li>What do we keep doing?</li>\n<li>What do we stop doing?</li>\n<li>What do we start doing?</li>\n</ul>\n<p>And from this discussion, the team creates a list of behaviors that they work on, collectively, over time. </p>\n<p>When commiting to action items, make sure you focus on 1 - 3. It is better to get a couple done, than commiting to more and not doing them. If it is difficult to come up with actions, try running experiments: Write down what you will do and what you want to achieve with that. In the following retro check if what you did achieved what you had planned. If it did not - you can learn out of the experiment and try out something else. </p>\n<p>A good approach to find out which topics should be discussed is <em>“Discuss and mention”</em>. It consists on a board with as many columns as people in the team (you can use something like Trello or just a regular whiteboard) and two more for the things to “discuss” and those to be “mentioned”. Everybody has 10 minutes to fill their column with things from the last sprint they want to highlight (these are represented with cards or Post-it’s). Afterwards, each team member explains each of their own items. Finally, each team member chooses which topics they think should be mentioned or discussed, by moving the card/post-it to the respectively column. The team then decides which topics should be discussed and talk about them for about 10 minutes.</p>\n<p>Invite the scrum team only to the Retrospective. (Delivery Team, Product Owner, Scrum Master). Discourage managers, stakeholders, business partners, etc. They are a distraction and can hinder the sense of openness the team needs.</p>\n<p>When conducting a retrospective make sure that in the first 5 - 10 minutes everyone says at least a couple of words. When team members speak up in the beginning of the meeting, it is more likely that they contribute during the whole meeting.</p>\n<p>During the retrospective it is important that the team stays productive. A simple way to keep people in a positive mood is to focus on what is in the control of the team.</p>\n<ul>\n<li>For a co-located team, index cards and post-its work great for this process.</li>\n<li>\n<p>For distributed teams, there are a variety of online tools and apps to facilitate the discussion</p>\n<ul>\n<li><a href=\"https://www.senseitool.com/home\">https://www.senseitool.com/home</a></li>\n<li><a href=\"http://www.leancoffeetable.com/\">http://www.leancoffeetable.com/</a></li>\n</ul>\n</li>\n</ul>\n<p>Action items should be notated on a board to make tracking progress visual and more distinguishable. Every new retrospective should start with reviewing the status of the action items decided upon during the <em>previous</em> restrospective.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>\n<p>Inspirations for preparing a retrospective:</p>\n<ul>\n<li><a href=\"https://plans-for-retrospectives.com/en/\">https://plans-for-retrospectives.com/en/</a></li>\n<li><a href=\"http://www.funretrospectives.com/\">http://www.funretrospectives.com/</a></li>\n</ul>\n</li>\n<li>\n<p>How to run experiments with the popcorn flow:</p>\n<ul>\n<li><a href=\"https://www.slideshare.net/cperrone/popcornflow-continuous-evolution-through-ultrarapid-experimentation\">https://www.slideshare.net/cperrone/popcornflow-continuous-evolution-through-ultrarapid-experimentation</a></li>\n</ul>\n</li>\n</ul>","fields":{"slug":"/agile/retrospectives/"},"frontmatter":{"title":"Retrospectives"}}},"pathContext":{"slug":"/agile/retrospectives/"}}

